<template>
  <div class="sections">
    <h1>服务费退款</h1>
    <div class="pl_body">
        <Row class="form_row">
            <Col :sm="22" :xs="24">
                <Form :model="formSel" class="downMenu" onsubmit="return false;">
                    <FormItem>
                        <Input v-model="formSel.loanNo" placeholder="借款编号"></Input>
                        <Input v-model="formSel.name" placeholder="客户姓名"></Input>
                        <Input v-model="formSel.mobile" placeholder="手机号"></Input>
                        <Button type="primary" @click="loanContent">查询</Button>
                    </FormItem>
                </Form>
            </Col>
            <Col :sm="2" :xs="24"><div class="float_right">
                <Button type="success" @click="exportData()"><Icon type="ios-download-outline"></Icon> 导出EXCEL</Button>
            </div></Col>
        </Row>
        <div class="table_show">
        <Table border :columns="columns1" :data="data1" ref="table"></Table></div>
        <Row :gutter="16" v-if="pageWin>=768">
            <Col :sm="5" :xs="24">
                <div class="total">共{{total}}页</div>
            </Col>
            <Col :sm="19" :xs="24" class="txt_right">
                <Page :page-size="pageSize" :total="totalRecords" show-elevator @on-change="changePage"></Page>
            </Col>
        </Row>
        <Page v-else :page-size="pageSize" :total="totalRecords" simple @on-change="changePage"></Page>
    </div>

    <Modal v-model="refundModel" title="退款处理">
      <div slot="footer">
        <Button type="text" size="large" @click="refundModel=!refundModel">取消</Button>
        <Button type="primary" size="large" @click="refundProcess">确定</Button>
      </div>
      <Form :model="formRefund" :label-width="90">
        <FormItem label="退款渠道">
          <Select v-model="formRefund.payChannel" placeholder="请选择">
            <Option value="110304">宝付原形</Option>
            <Option value="001301">支付宝（益倍嘉）原形</Option>
          </Select>
        </FormItem>
        <FormItem>
          <Input v-model="formRefund.refundAmt" placeholder="退款金额"></Input>
        </FormItem>
        <FormItem>
          <Input v-model="formRefund.remark" placeholder="退款原因"></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>

export default {
  name: 'sections',
  data () {
    return {
        formRefund: {
          refundAmt: 0,
          remark: '',
          payChannel: '',
          flowNo: '',
          acctId: ''
        },
        refundModel: false,
        visible: false,
        vs: false,
        vs1: false,
        btnSelct:'全部',
        model1:false,
        tbWidth:100,
        total:0,
        totalRecords:0,
        pageStart:0,
        pageSize:3,
        merchant:'',
        acctId:'',
        loanId:'',
        operatorId:0,
        formSel:{
            name:'',
            loanNo:'',
            mobile:''
        },
        formCount:{
            why:'',
            desc:''
        },
        backDetail:{},
        ruleCount:{
            why:[
                {require:true,type:'string',message:'请选择扣款原因',trigger:'blur'}
            ],
            desc:[
                {require:true,type:'string',message:'备注不能为空',trigger:'blur'}
            ]
        },
      ruleMoney:{
        money:[
          {require:true,type:'string',message:'请选择扣款金额',trigger:'blur'}
        ]
      },
        columns1: [
        {
            title: '借款编号',
            key: 'loanNo'
        },
        {
            title: '客户姓名',
            key: 'name'
        },
        {
          title: '手机号',
          key: 'mobile'
        },
        {
          title: '身份证',
          key: 'idCard'
        },
        {
          title: '性别',
          key: 'sex'
        },
        {
          title: '年龄',
          key: 'age'
        },
        {
          title: '交易号',
          key: 'tradeNo'
        },
        {
          title: '服务费金额',
          key: 'chargeAmount'
        },
        {
            title: '付费时间',
            key: 'crtTm'
        },
        {
            title: '付费通道',
            key: 'merchant'
        },
        {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
                return h('div', [
                    h('Button', {
                        props: {
                            type: 'primary',
                            size: 'small'
                        },
                        on: {
                            click: () => {
                                this.show(params.index);
                            }
                        }
                    }, '退费')
                ]);
            }
        }],
        data1: []
        }
    },
    created:function(){

        this.loadPage();

        if($(window).width()<768){
            var arr=this.columns1;
            for(var i=0;i<arr.length;i++){
                arr[i]['width']=this.tbWidth;
            }
            arr[arr.length-1]['fixed']='right';
            this.columns1=arr;
        }
    },
    computed :{
        pageWin(){
            return Math.max(window.innerWidth,document.documentElement.clientWidth,document.body.clientWidth);
        }
    },
    mounted(){

    },
    methods: {
      showMenu(val){
          if(val!='全部' && val!=''){
              this.formSel.selectTp=' : '+val;
          }else{
              this.formSel.selectTp='';
          }
      },
      orShow(visible){
          this.vs=visible;
      },
      showMenu1(val){
          if(val!='全部' && val!=''){
              this.formSel.selectCm=' : '+val;
          }else{
              this.formSel.selectCm='';
          }
      },
      orShow1(visible){
          this.vs1=visible;
      },
      dateChange1(date){
          this.formSel.payTm=date;
      },
      exportData () {
        var that = this;
        that.data1=[];
        var detail = {};
        detail['loanInput']=that.formSel.input;
        detail['beginDate']=that.formSel.payTm[0];
        detail['endDate']=that.formSel.payTm[1];
        detail['selectCm']=that.formSel.selectCm;

      },
      changePage(){
          /*this.data1=this.*/
      },
      show(index){
        let self = this
        self.formRefund.refundAmt = self.data1[index].chargeAmount
        self.formRefund.flowNo = self.data1[index].flowNo
        self.formRefund.acctId = self.data1[index].acctId

        self.refundModel = true
      },
      refundProcess(){
        let self = this

        let refundAmt = self.formRefund.refundAmt
        let remark = self.formRefund.remark
        let payChannel = self.formRefund.payChannel
        let flowNo = self.formRefund.flowNo
        let acctId = self.formRefund.acctId

        if(payChannel==null || payChannel==''){
          alert("退款渠道不能为空")
          return
        }

        if(refundAmt==null || refundAmt==''){
          alert('退款金额不能为空')
          return
        }

        if(remark==null || remark == ''){
          alert('退款原因不能为空')
          return
        }

        let params = {
          refundAmt: refundAmt,
          remark: remark,
          refundChannel: payChannel,
          flowNo: flowNo,
          acctId: acctId
        }

        this.axios
          .post(
            '/refund/chargeRefund',
            params,
            {
              headers:{'Content-Type':'application/json'}
            })
          .then(function(resp){
            if(resp.data.resCode == 1){
              alert("操作成功")
              self.loanContent()
            }

          })
          .catch(function(resp){
            console.log(JSON.stringify(resp))
            self.$Message.warning('系统异常');
          });

      },
      countOk(){

      },

      loanContent(){
        var self = this;

        var name = this.formSel.name
        var mobile = this.formSel.mobile
        var loanNo = this.formSel.loanNo

        var params = {
          name : name,
          mobile : mobile,
          loanNo : loanNo
        }

        this.axios
          .post(
            '/refund/chargeQuery',
            params,
            {
                headers:{'Content-Type':'application/json'}
            })
          .then(function(respon){
              if(respon.data.resCode == 1){
                respon.data.result.forEach(function (element) {
                  element.chargeAmount = element.chargeAmount/100
                  let d = new Date(element.crtTm)
                  element.crtTm = (d.getFullYear()) + "-" + (d.getMonth() + 1) + "-" + (d.getDate());
                },this);

                self.data1 = respon.data.result;
              }

            })
          .catch(function(resp){
            console.log(JSON.stringify(resp))
            self.$Message.warning('系统异常');
          });
      },

      changePage(pageNum){
        this.pageStart = (pageNum - 1) * this.pageSize;
        this.loanContent();
      }
    }
}
</script>



